随着病情发展，终年均有咳嗽、咳痰，而以秋冬为剧。 本病早期多无特殊体征，大多数在肺底部可听到湿性和干性罗音，有时咳嗽或咳痰后消失，长期发作者可导致肺气肿。 ")])])])])],1)])])
